/* CONTACTS */
.contacts-section ul {
  padding: 0;
  margin: 0;
}

.list {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

/* MOBILE */

@media screen and (max-width: 767px) {
  .contacts-section {
    padding-top: 50px;
    padding-bottom: 65px;
    background-color: #fff5f6;
  }

  .contacts-list {
    display: block;
    justify-content: center;
  }

  .contacts-list-item {
    /* margin-top: -170px; */
    background: #ffffff;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    padding-top: 43px;
    padding-right: 20px;
    padding-bottom: 43px;
    padding-left: 20px;
    width: 100%;
    max-width: 220px;
    margin-right: auto;
    margin-left: auto;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  .kind {
    display: inline-block;
    margin-top: 0px;
    margin-bottom: 25px;
    padding: 0 15px;
    background: #f0d1a5;
    border-radius: 6px;

    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;

    align-items: center;
    text-align: center;
    letter-spacing: 0.04em;
    color: #ffffff;
    text-transform: uppercase;
  }

  .kind-food-truck {
    display: inline-block;
    margin-top: 0px;
    margin-bottom: 25px;
    padding: 0 15px;
    background: #c2e297;
    border-radius: 6px;

    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;

    align-items: center;
    text-align: center;
    letter-spacing: 0.04em;
    color: #ffffff;
    text-transform: uppercase;
  }

  .city-name-title {
    margin-top: 0;
    margin-bottom: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;

    letter-spacing: 0.04em;

    color: #000000;
  }

  .city-name-description {
    margin-top: 0;
    margin-bottom: 25px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;

    letter-spacing: 0.04em;
    color: #907e82;
  }

  .work-days {
    margin-top: 25px;
    margin-bottom: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;

    letter-spacing: 0.04em;
    color: #000000;
  }

  .work-time {
    margin-top: 0;
    margin-bottom: 25px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    color: #ffa5ba;
  }

  .phone-number {
    margin-top: 25px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.5em;

    color: #907e82;
  }

  .mail-address {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.5em;

    color: #907e82;
  }

  .border {
    border-bottom: 1px solid #e1e1e1;
  }

  .contacts-btn-1 {
    display: flex;
    padding: 10px 18px;
    background: #d41342;
    border-radius: 22px;
    border: none;
    cursor: pointer;

    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.31em;

    align-items: center;
    text-align: center;

    color: #ffffff;
  }

  .contacts-btn-2 {
    display: flex;
    padding: 10px 35px;
    background: #ffffff;
    border-radius: 22px;
    border: none;
    cursor: pointer;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 21px;

    align-items: center;
    text-align: center;

    color: #d41443;

    &:hover {
      background: #ecebeb;
    }
  }

  .contacts-buttons {
    display: flex;
    justify-content: center;

    margin-top: 54px;
  }

  .contacts-btn-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .contacts-btn-list-item:not(:last-child) {
    margin-bottom: 10px;
  }

  .icon-arrow {
    margin-left: 15px;
    fill: currentColor;
  }
}

